class HelloWorld extends HTMLElement {
  constructor() {
    super();
  }

  connectedCallback() {
    this.innerHTML = `
    <div>
        <p>
        ${this.innerText}
        </p> 
        <span>
            ${this.getAttribute()}
        </span>
    </div>
    `;
    
  }
}

customElements.define("hello-world", HelloWorld);
